import React from 'react';
import ScrollableAnchor from 'react-scrollable-anchor';
import ScrollAnimation from 'react-animate-on-scroll';

import { Box, Button, Hidden, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

import { Parallax } from 'react-scroll-parallax';
import left from '../../../data/backs/1.svg';
import right from '../../../data/backs/2.svg';

const useStyles = makeStyles((theme) => ({
    root: {
        paddingTop: '150px',
    },
    shape: {
        zIndex: '-1',
        [theme.breakpoints.down('xs')]: {
            visibility: 'hidden',
        },
        [theme.breakpoints.down('md')]: {
            opacity: '0.4',
        },
    },
}));

const Header = ({ headerInfo }) => {
    const classes = useStyles();

    const rightStyle = {
        position: 'absolute',
        right: 0,
        top: 0,
    };

    const leftStyle = {
        position: 'absolute',
        left: 0,
        top: 0,
    };

    return (
        <ScrollableAnchor id={'header'}>
            <ScrollAnimation animateIn="fadeIn" animateOnce={true}>
                <Box display="flex" alignItems="center" flexDirection="column" className={classes.root}>
                    <Hidden smDown>
                        <Parallax y={[100, 0]} styleInner={leftStyle}>
                            <img
                                alt="second"
                                src={left}
                                className={classes.shape}
                                style={{ transform: 'scale(-1, 1)' }}
                            />
                        </Parallax>
                        <Parallax y={[100, 0]} styleInner={rightStyle}>
                            <img
                                alt="second"
                                src={right}
                                className={classes.shape}
                                style={{ transform: 'scale(-1, 1)' }}
                            />
                        </Parallax>
                    </Hidden>
                    <img alt="field" src={headerInfo.icon} height="45" />
                    <Typography variant="h2" component="h1" gutterBottom align="center">
                        {headerInfo.title}
                    </Typography>
                    <Typography variant="subtitle1" component="p" gutterBottom style={{ width: '75%' }} align="center">
                        {headerInfo.description}
                    </Typography>
                    <Button
                        variant="contained"
                        href="#contact"
                        color="primary"
                        disableElevation
                        style={{ marginTop: 25 }}
                    >
                        Start out
                    </Button>
                </Box>
            </ScrollAnimation>
        </ScrollableAnchor>
    );
};

export default Header;
