import React, { useState } from 'react';
import ScrollAnimation from 'react-animate-on-scroll';
import ScrollableAnchor from 'react-scrollable-anchor';

import Project from '../../components/Project';

import { Box, Button, Grid, Hidden, Typography } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import { Link } from 'react-router-dom';

const FieldButton = withStyles((theme) => ({
    root: {
        textTransform: 'uppercase',
        padding: 0,
        paddingLeft: 5,
        paddingRight: 5,
        margin: 5,
        marginLeft: 10,
        minHeight: 0,
        minWidth: 0,
        marginRight: 10,
        letterSpacing: '0.75px',
    },
    contained: {
        color: theme.palette.primary.main,
        background: theme.palette.primary.transparent.light,
        '&:hover': {
            background: theme.palette.primary.transparent.dark,
        },
    },
}))(Button);

const Fields = ({fields}) => {

    const [activeField, setActiveField] = useState(0);

    return (
        <ScrollableAnchor id={'projects'}>
            <Box my={12}>
                <ScrollAnimation animateIn="fadeIn" animateOnce={true} delay={200}>
                    <Grid item spacing={2} style={{ marginTop: 10 }} align="center">
                        <Typography variant="h3" align="center" component="h2" gutterBottom>
                            Our fields of knowledge
                        </Typography>
                    </Grid>
                    <Grid container spacing={2} alignItems="center">
                        <Grid item xs={12} md={12} align="center">
                            {fields.map((field, index) => {
                                return (
                                    <FieldButton
                                        key={index}
                                        size="small"
                                        variant={activeField === index ? 'contained' : 'text'}
                                        color="primary"
                                        disableElevation
                                        onClick={() => setActiveField(index)}
                                    >
                                        {field.button}
                                    </FieldButton>
                                );
                            })}
                        </Grid>
                        <Grid item xs={12} md={6}>
                            <Hidden smDown>
                                <Typography variant="h5" component="h3" gutterBottom>
                                    {fields[activeField].header}
                                </Typography>
                                <Typography variant="subtitle1" component="p" gutterBottom>
                                    {fields[activeField].description}
                                </Typography>
                                <Button
                                    component={Link}
                                    color="primary"
                                    variant="contained"
                                    disableElevation
                                    to={`${fields[activeField].path}#header`}
                                >
                                    Find more
                                </Button>
                            </Hidden>
                            <Hidden mdUp>
                                <Box display="flex" alignItems="center" flexDirection="column">
                                    <Typography variant="h5" component="h3" gutterBottom align="center">
                                        {fields[activeField].header}
                                    </Typography>
                                    <Typography variant="subtitle1" component="p" gutterBottom align="center">
                                        {fields[activeField].description}
                                    </Typography>
                                    <Button
                                        component={Link}
                                        color="primary"
                                        variant="contained"
                                        disableElevation
                                        to={`${fields[activeField].path}#header`}
                                    >
                                        Find more
                                    </Button>
                                </Box>
                            </Hidden>
                        </Grid>
                        <Grid item xs={12} md={6}>
                            <Project data={fields[activeField].project} />
                        </Grid>
                    </Grid>
                </ScrollAnimation>
            </Box>
        </ScrollableAnchor>
    );
};

export default Fields;
